<template>
  <div>
      <v-snackbar
            v-model="snackbar"
            top
            right
            :color="color"
            >
            {{msg}}
            <v-btn
            color="black"
            text
            @click="snackbar = false"
            >
            
            </v-btn>
  
            </v-snackbar>
     
      
      <v-expansion-panels v-model="openedPanel">
    <v-expansion-panel>
      <v-expansion-panel-header>
        <v-card-title class="text-center" style="display: block;">{{title}}</v-card-title>
        
      </v-expansion-panel-header>
      <v-expansion-panel-content>
        <v-form class="multi-col-validation" @submit.prevent="onUploadFile" method="POST"  enctype="multipart/form-data" autocomplete="off">
        <v-row>
          <v-col 
          cols="12"
            md="6">
            <div >
               <img v-if="previewurl" :src="previewurl"  style="width:75px;"/>
               <img v-else class=" " v-show="EditServiceImage" :src="ServerURL+'uploads/service/'+EditServiceImage" alt="" style="width:50px;border:1px solid #000;" @error="$event.target.src='https://bitsofco.de/content/images/2018/12/broken-1.png'">
          
            </div>
            <label>Service Image</label><br>
            <input type="file" ref="imgser"  @change="onFileChange" />
          </v-col>
          <v-col 
            cols="12"
            md="6">
            <div >
               <img v-if="previewurl_" :src="previewurl_"  style="width:75px;"/>
               <img v-else class=" "  v-show="EditSolutionImage" :src="ServerURL+'uploads/solution/'+EditSolutionImage" alt="" style="width:50px;border:1px solid #000;" @error="$event.target.src='https://bitsofco.de/content/images/2018/12/broken-1.png'">
          
            </div>
            <!-- {{EditSolutionImage}} -->
            <label>Solution Image</label><br>
            <input type="file" ref="imgsol"  @change="onFileChange_S" />
          </v-col>
          <v-col
            cols="12"
            md="6"
            hidden
          >
            <v-text-field
              v-model="S_.Service_Date"
              label="Service Date"
              readonly
              
              dense
              hide-details
            ></v-text-field>
          </v-col>
          <v-col
            cols="12"
            md="6"
          >
          <!--  -->

            <v-combobox
           
            @change="Get_client_info();Get_client_service()"
            v-model="S_.ClientName"
          :items="customers"
          label="Client Name"
          clearable
          dense
        ></v-combobox>
        <div :style="'background:'+serv_color+';font-color:#fff;width: 100%;padding:2px;'">
         
        </div>
          </v-col>
    
          <v-col
            cols="12"
            md="6"
          >
          <v-combobox
            v-model="S_.Region"
          :items="Region"
          label="Region"
          clearable
          dense
        ></v-combobox>
            
          </v-col>
          
          <v-col
            cols="12"
            md="6"
            hidden
          >
            <v-text-field
              v-model="S_.ReportedTime"
              label="Reported Time"
              
              dense
              hide-details
            ></v-text-field>
          </v-col>
    
          <v-col
            cols="12"
            md="6"
            hidden
          >
         
            <v-text-field
              v-model="S_.Finished_Time"
              label="Finished Time"
              
              dense
              hide-details
            ></v-text-field>

            
          </v-col>
    
          <v-col
            cols="12"
            md="6"
          >
          <v-combobox
          v-model="S_.Status"
          :items="Status"
          label="Status"
          clearable
          @change="setFinished_time"
          dense
         
          
        ></v-combobox>
          
          </v-col>
          <v-col
            cols="12"
            md="6"
          >

          <v-combobox
          v-model="S_.Priority"
          :items="Priority_"
          label="Priority"
          clearable
          dense
        ></v-combobox>
          
         
          </v-col>
          <v-col
            v-if="UserRole == 'Administrator' && edited == true"
            cols="12"
            md="6"
          >

          <v-combobox
          v-model="S_.AttendedBy"
          :items="Users_"
          label="Assigned To"
          clearable
          dense
        ></v-combobox>
          
         
          </v-col>
          <v-col
            v-if="UserRole == 'Administrator' && edited == true"
            cols="12"
            md="6"
          >

         
          <v-text-field
            v-model="time"
            label="Finished Time"
            readonly
            dense
          ></v-text-field>
       
         
          </v-col>
          <v-col
            cols="12"
            md="6"
            style="padding-top: 0px !important;"
          >
          <v-textarea
          style="resize: none;"
          v-model="S_.ServiceDecription"
      label="Service Decription"
    ></v-textarea>
          </v-col>
          <v-col
            cols="12"
            md="6"
            style="padding-top: 0px !important;"
          >
          <v-textarea
          style="resize: none;"
          v-model="S_.SolutionDecription"
      label="Solution Decription"
    ></v-textarea>
          </v-col>
    
         
    
          
    
          <v-col cols="12">
            <v-btn color="primary" type="submit" v-if="!edited">
              Submit
            </v-btn>
            <!-- updateservice -->
            <v-btn color="primary" type="button" v-if="edited" @click="editupload" >
              Update
            </v-btn>
            <v-btn
              type="button"
              @click="FormReset"
              class="mx-2"
            >
              Reset
            </v-btn>
          </v-col>
        </v-row>
      </v-form>
      </v-expansion-panel-content>
    </v-expansion-panel>
      </v-expansion-panels>
     
  
      <!-- Data table -->
      
      <v-card>
      <v-card-title>
        <span class="text-center ">Services</span>
        <v-spacer></v-spacer>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
         item-key="id"
        :headers="headers"
        :items="services"
        :search="search"
        
      >  
      <template v-slot:[`item.Img`]="{ item }" >
          <img width="50" :src="require(`../../../../../../backend/uploads/service/${item.Img}`)" @error="$event.target.src='https://bitsofco.de/content/images/2018/12/broken-1.png'"/>
      </template>
      
      
        <template v-slot:[`item.actions`]="{item}" >
          <div >
            <v-btn color="success" class="mx-2" :disabled="isAdmin != 'Administrator'" fab x-small @click="editService(item)" outlined>
              <i class="fa fa-pencil" aria-hidden="true"></i>
            </v-btn>
            <v-btn :disabled="isAdmin != 'Administrator'" color="red" fab x-small @click="deleteService(item._id)" outlined>
              <i class="fa fa-trash" aria-hidden="true"></i>
            </v-btn>
            <v-btn :disabled="isAdmin != 'Administrator'" color="info" class="mx-2" fab x-small @click="details_(item._id)" outlined>
              <i class="fa fa-eye" aria-hidden="true"></i>
            </v-btn>
            
           
          </div>
         
           
          </template>
          <template v-slot:no-results>
              <v-alert :value="true" color="danger"  dark>
                Your Search {{search}} does not exist.
              </v-alert>
          </template>
  
  </v-data-table>
    </v-card>
  </div>
    </template>
    
    <script>
      import axios from "axios";
      import { mdiAccountOutline, mdiEmailOutline, mdiCellphone, mdiLockOutline } from '@mdi/js'
    export default {
      data() {
      
        return {
          icons: {
        mdiAccountOutline,
        mdiEmailOutline,
        mdiCellphone,
        mdiLockOutline,
      },
          snackbar:false,
          msg:'',
          color:'',
          title:'Add Services',
          search:'',
          switch1:'',
          isAdmin:localStorage.getItem("Userrole"),
          headers: [
          // {text: 'Sl No', value: 'SlNo', sortable: false, width: '150px'},
          {text: 'Service', value: 'Img', sortable: false},
          // {text: 'Solution', value: 'Imgs', sortable: false},
          {text: 'Date', value: 'Service_Date', sortable: true},
          {text: 'Client Name', value: 'ClientName', sortable: true},
          {text: 'Attended By', value: 'AttendedBy', sortable: true},
          {text: 'Region', value: 'Region', sortable: true},
          {text: 'Reported Time', value: 'ReportedTime', sortable: true},
          // {text: 'Finished Time', value: 'Finished_Time', sortable: true},
          {text: 'Status', value: 'Status', sortable: true},
          {text: 'Decription', value: 'ServiceDecription', sortable: true},
          {text: 'Action', value: 'actions', sortable: true, width: '180px'},
      ],
      previewurl:'',
      previewurl_:'',
         S_:{
            AttendedBy:localStorage.getItem("loggedUser"),
            Service_Date:'',
            ClientName:'',
            Region:'',
            ReportedTime:'',
            pending_time:'',
            doing_time:'',
            done_time:'',
            Status:'',
            ServiceDecription:'',
            SolutionDecription:'',
            Img:'broken-1.png',
            Priority:'',
            Imgs:'broken-1.png',
           
    
         },
         ServerURL:'https://secret-chamber-23447.herokuapp.com/',
         EditServiceImage:'',
         EditSolutionImage:'',
         docs_link:'http://docs.google.com/gview?url=',
         openedPanel: null,
         E_id:'',
         Region:[
          "Oman",
          "Uae", 
          "Kerala"
          ],
          Status:[
          "Pending",
          "Doing", 
          "Done"
          ],
          Priority_:[
          "High",
          "Medium", 
          "Low"
          ],
          location:[],
          services:[],
          edited: false,
          customers:[],
          selectedFile:'',
          selectedFile_:'',
          client_info:[],
          serv_color:'',
          UserRole:localStorage.getItem('Userrole'),
          Users_:[],
          time:''
        }
      },
      mounted()
      {
          this.GetServices()
          this.date_function()
          this.GetCustomers()
          this.GetUsers()
      },
      methods:{
        setFinished_time()
        {
          var currentDate = new Date();
          if(this.S_.Status == "Pending")
          {
            this.S_.pending_time = currentDate.toLocaleTimeString()
          }
          if(this.S_.Status == "Doing")
          {
            this.S_.doing_time = currentDate.toLocaleTimeString()
          }
          if(this.S_.Status == "Done")
          {
            this.S_.done_time  = currentDate.toLocaleTimeString()
          }
          

        },

        onFileChange(e) {
      const selectedFile = e.target.files[0]; // accessing file
      this.selectedFile = selectedFile;
      this.previewurl = URL.createObjectURL(selectedFile);
      this.EditServiceImage = selectedFile
      this.S_.Img = this.selectedFile.name
    },
    onFileChange_S(e) {
      const selectedFile_ = e.target.files[0]; // accessing file
      this.selectedFile_ = selectedFile_;
      this.previewurl_ = URL.createObjectURL(selectedFile_);
      this.S_.Imgs = this.selectedFile_.name
    },
    editupload()
    {
      if(this.selectedFile)
      {
        this.onUploadFile()
      }else{
         if(this.selectedFile_)
          {
            this.onUploadFile_()
          }else{
            if(this.edited)
          {
            this.updateservice()

          }else{
            this.createservice()
          }
          }
      }
    },
    onUploadFile() {
      const formData = new FormData();
      formData.append("file", this.selectedFile);
      // formData.append("file_s", this.selectedFile_);

     // sending file to the backend
      axios
        .post("api/postAPI/upload", formData)
        .then(res => {
          console.log(res);
          // this.createservice()
          if(this.selectedFile_)
          {
            this.onUploadFile_()
          }else{
            if(this.edited)
          {
            this.updateservice()

          }else{
            this.createservice()
          }
          }
         
        })
        .catch(err => {
          console.log(err);
        });
    },
    onUploadFile_() {
      const formData = new FormData();
      // formData.append("file", this.selectedFile);
      formData.append("file", this.selectedFile_);

     // sending file to the backend
      axios
        .post("api/postAPI/uploads", formData)
        .then(res => {
          console.log(res);
          if(this.edited)
          {
            this.updateservice()

          }else{
            this.createservice()
          }
         
        })
        .catch(err => {
          console.log(err);
        });
    },
        date_function: function () {
   
   var currentDate = new Date();
   this.S_.Service_Date = currentDate.toLocaleDateString("en-US");
   this.S_.ReportedTime = currentDate.toLocaleTimeString();
  //  this.S_.Finished_Time = currentDate.toLocaleTimeString();

  //  this.S_.Service_Date = new Date().toJSON().slice(0,10).replace(/-/g,'/');

},
          Get_client_info: async function(){
            if(this.S_.ClientName != null)
            {
              let apiURL = 'https://maraidemoapi.linkwayapps.com/api/Customer/GetCustomerByName?CustName='+this.S_.ClientName;
              axios.get(apiURL).then(res => {
              this.client_info = res.data;
              this.S_.Region = res.data.location
              }).catch(error => {
                console.log(error)
             });
            }
          },
          GetUsers: async function(){
            if(this.UserRole == 'Administrator')
            {
              let apiURL = 'api/postAPI/loadusers';
              axios.get(apiURL).then(res => {
                for(let i = 0;i<res.data.length;i++)
                {
                  this.Users_.push(res.data[i].Name);
                }
              
              }).catch(error => {
                console.log(error)
             });
            }
          },
          Get_client_service: async function(){
            if(this.S_.ClientName != null)
            {
              let apiURL = 'https://maraidemoapi.linkwayapps.com/api/Customer/GetCustomerForService?CustName='+this.S_.ClientName;
              axios.get(apiURL).then(res => {
              // console.log(res.data)
              this.serv_color = res.data.serviceColor
              }).catch(error => {
                console.log(error)
             });
            }
             
          },

          GetServices: async function(){
            let apiURL;
            if(this.UserRole == 'Administrator')
            {
              apiURL = `api/postAPI/loadServices`;

            }else
            {
              apiURL = `api/postAPI/loadServices_/${this.S_.AttendedBy}`;
            }
              
              axios.get(apiURL).then(res => {
              this.services = res.data;
              }).catch(error => {
                console.log(error)
             });
          },
          GetCustomers: async function(){
              let apiURL = 'https://maraidemoapi.linkwayapps.com/api/customer';
              axios.get(apiURL).then(res => {
              this.customers = res.data;
              }).catch(error => {
                console.log(error)
             });
          },
          createservice()
          {
      
          if(this.S_.Service_Date != "" && this.S_.ClientName != "" && this.S_.Region !=  "" && this.S_.ReportedTime != ""  && this.S_.AttendedBy != "")
          {
           
        
          let apiURL = 'api/postAPI/create-service';
          axios.post(apiURL, this.S_).then(res => {
          if(res.data != null)
          {
            this.GetServices()
            this.FormReset()
            console.log(res.data)
            this.color ='success'
        this.msg = ' Service Added Successfully.'
        this.snackbar = true;

           
          }else
          {
            console.log(res.data)
          }
          
          
        }).catch(error => {
          console.log(error)
        });
      }else
      {
        this.color ='error'
        this.msg = ' Please enter all Values.'
        this.snackbar = true;
      }
          },
          FormReset()
          {
            // this.S_.AttendedBy = '',
            // this.S_.Service_Date='',
            this.S_.ClientName='',
            this.S_.Region='',
            this.S_.Img = '',
            this.EditServiceImage =''
            this.$refs.imgser.value = null;
            this.$refs.imgsol.value = null;
            this.EditSolutionImage =''
            // this.S_.ReportedTime='',
            // this.S_.Finished_Time='',
            this.S_.Status='',
            this.S_.Priority='',
            this.S_.Imgs='',
            this.S_.ServiceDecription='',
            this.S_.SolutionDecription ='',
            this.S_.pending_time = ''
            this.S_.doing_time = ''
            this.S_.done_time  = ''
            this.previewurl = ''
            this.previewurl_ = ''
             this.E_id = ''
             this.title = "Add Service "
             this.edited = false;
  
          },
          editService(item)
          {
            this.previewurl = ''
            this.previewurl_ = ''
             this.title = "Edit Service "
             this.edited = true;
             this.openedPanel = 0
             this.EditServiceImage = item.Img,
             this.EditSolutionImage = item.Imgs,
             this.S_.AttendedBy = item.AttendedBy,
            this.S_.Service_Date =  item.Service_Date,
            this.S_.ClientName= item.ClientName,
            this.S_.Region= item.Region,
            this.S_.ReportedTime= item.ReportedTime,
            this.S_.Priority= item.Priority,
            this.S_.Img= item.Img,
            this.S_.Imgs= item.Imgs,
            // this.EditServiceImage = item.Img,
            //  this.EditSolutionImage = item.Imgs,
            // this.S_.Finished_Time= item.Finished_Time,
     
            this.S_.pending_time = item.pending_time,
          
            this.S_.doing_time = item.doing_time,
          
            this.S_.done_time  = item.done_time,
   
            this.S_.Status= item.Status,
            this.S_.ServiceDecription= item.ServiceDecription,
            this.S_.SolutionDecription= item.SolutionDecription,
            this.E_id = item._id,
            this.time = 'Pending Time : '+item.pending_time +' - Doing Time : '+item.doing_time+' - Done Time : '+item.done_time

  
          },
          updateservice()
          {
          //  console.log(this.S_)
           let apiURL = `api/postAPI/update-service/${this.E_id}`;
          axios.post(apiURL, this.S_).then((res) => {
          // console.log(res)
          if(res.status == 200)
          {
              this.title = "Add Service"
              this.GetServices()
              this.FormReset()
          }else
          {
              console.log(res)
          }
          // this.close();
          // this.loadCustomer();
          this.color ='success'
          this.msg = ' Updated Successfully.'
          this.snackbar = true;
        }).catch(error => {
          console.log(error)
          console.log(this.$route.params.id)
        })
          },
          deleteService(id){
           this.$swal({
                      title: 'Are you sure? ',
                      text: "You will not be able to go back",
                      icon: 'warning',
                      showCancelButton: true,
                      confirmButtonColor: '#00b894',
                      cancelButtonColor: '#d63031',
                      confirmButtonText: 'Yes',
                      cancelButtonText: 'No',
                      }).then((result) => {
                          // Send request to the server
                           if (result.value) {
                                 try {
                  let apiURL = `api/postAPI/delete-service/${id}`;
                  let indexOfArrayItem = this.services.findIndex(i => i._id === id);
                  axios.delete(apiURL).then(() => {
                          this.services.splice(indexOfArrayItem, 1);
                      }).catch(error => {
                          console.log(error)
                      });
                  //  this.$swal(
                  //          'Deleted!',
                  //          'User has been deleted.',
                  //          'success'
                  //         )
                          //Notification error
                  this.color = 'error'
                  this.msg = "Service has been deleted."
                  this.snackbar = true;
                  //Notification error
              }catch (error) {
               this.$swal("Failed!", "There was something wrong."+error, "warning");
              }
                           }
                      })
    
              },
              Checkext(filename)
              {
                this.ext =  filename.split('.').pop();
                return this.ext
              }
      }
    }
    </script>
    